@import 'vars';

.docs-root {
    .color-green {
        color: $greensock-green;
    }
    .block-container {
        min-width: auto;
        .block {
            @media (max-width:1200px) {
                width: auto;
                margin: 0 20px;
            }
        }
    }
}

.docs-head-container {
    height: auto;

    .breadcrumbs {
        padding: 14px 0;
    }

    .right {
        float: right;
    }
}

.docs-main-container {
    .docs-main {
        position: relative;
        opacity: 1;
        visibility: visible;

        .docs-nav {
            z-index: 1;

            .nav-underlay {
                display: none;
            }

            .handle-mobile {
                display: none;
            }

            @media (max-width: 1023px) {
                position: absolute;
                top: -20px;
                left: -320px;
                z-index: 11111;

                .nav-underlay {
                    display: block;
                    position: fixed;
                    background: rgba(0, 0, 0, .4);
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    visibility: hidden;
                    transition: opacity 0.3s;

                    &.open {
                        opacity: 1;
                        visibility: visible;
                    }
                }

                .handle-mobile {
                    display: block;
                    position: absolute;
                    top: 0;
                    right: 0;
                    background: $greensock-green;
                    z-index: 1;
                    padding: 10px 10px;
                    right: -43px;
                    border: 1px solid #ccc;
                    border-left: none;
                    border-top-right-radius: 6px;
                    border-bottom-right-radius: 6px;
                    cursor: pointer;

                    .open {
                        g {
                            fill: #fff;
                        }
                    }
    
                    .close {
                        display: none;
                    }
                }

                &.closed-fixed {
                    position: fixed;
                    top: 10px;
                    left: -320px;
                    .handle-mobile {
                        right: -63px;
                    }
                }

                &.open {

                    .handle-mobile {
                        right: 0;
                        border-bottom: 0;
                        background: #fff;
    
                        .open {
                            display: none;
                        }
                        .close {
                            display: block;
                        }
                    }
                }
            }
        }

        .docs-nav .panels .side-container {
            position: relative;

            .side .nav-panels .inner-panel {
                .item {
                    transition: 0.3s ease-in-out;

                    p {
                        transition: 0.3s ease-in-out;
                    }
                }

                .item-class,
                .item-element,
                .item-folder,
                .item-page {
                    &:hover {
                        background-color: #f8f8f8;

                        &:before {
                            width: 10px;
                        }

                        p {
                            transform: translate3d(10px, 0, 0);
                        }
                    }
                }

                .item-element {
                    &:hover {
                        background-color: #3b3b3b;
                    }
                }

                .item-class {
                    &:before {
                        content: '';
                        display: inline-block;
                        width: 3px;
                        height: 100%;
                        background: $greensock-green;
                        position: absolute;
                        top: 0;
                        left: 0;
                        transition: 0.3s ease-in-out;
                    }
                }

                .item-folder {
                    &:before {
                        background: none;
                    }
                }

                .item-header {
                    height: auto;

                    p {
                        font-size: 1.1em;
                        font-weight: bold;
                        padding: 0.5em 30px;
                    }
                }

                .item-page {
                    &:before {
                        content: '';
                        display: inline-block;
                        width: 3px;
                        height: 100%;
                        background: orange;
                        position: absolute;
                        top: 0;
                        left: 0;
                        transition: 0.3s ease-in-out;
                    }
                }

                .doc-element {
                    display: block;

                    a {
                        display: block;
                        text-decoration: none;
                    }
                }
            }
        }

        .docs-content {
            overflow: hidden;
            width: calc(100% - 300px - 40px); //300px for nav, 40 in between

            @media (max-width: 1023px) {
                width: 100%;
                margin-top: 30px;
            }

            h1,
            h4 {
                a {
                    color: $dark-gray;
                    text-decoration: none;
                }
            }

            .docs-feature {
                display: flex;

                .left {
                    width: auto;
                    padding-right: 40px;
                }

                .right {
                    width: auto;
                    padding-left: 40px;

                    p {
                        padding: 38px 52px 38px 0;
                    }
                }

                @media (max-width: 599px) {
                    flex-direction: column;
                    .left {
                        border-right: none;
                        margin: 20px 0 0;
                    }
                    .right {
                        padding: 0 24px 20px;
                        p {
                            padding-top: 20px;
                            padding-bottom: 0;
                        }
                    }
                }
            }

            .class-intro-text {
                height: auto;
            }

            .show-more-line {
                .show-more-intro {
                    display: none;
                }

                .fade {
                    display: none;
                }
            }

            .element-table {
                .elements {
                    > div.element:first-child {
                        border: none;
                    }

                    &.parameters {
                        .element {
                            .left {
                                width: auto;
                                padding: 14px 20px;
                            }
                        }
                    }

                    .config-object {
                        // overflow: hidden;
                        .config-object-props {
                            height: auto;
                            // overflow: visible;
                        }

                        .show-more {
                            transition: background-color 0.3s;

                            &:hover {
                                background-color: #777;
                            }
                        }
                    }

                    .show-more-container {
                        .fade {
                            transition: opacity 1s;
                            pointer-events: none;
                        }

                        &.opened {
                            .fade {
                                opacity: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    .divider-line {
        width: percentage(820 / 830);
        height: 2px;
    }
}

.config-object,
.config-object-shortcode {
    padding-left: 1em !important;

    .config-object-props {
        padding: 0 0 0 1em;

        .prop {
            span p {
                display: inline;
            }
        }

        .prop-with-more {
            .short {
                display: inline;
            }

            .long {
                display: none;
            }

            .more-less {
                cursor: pointer;

                &:before {
                    content: '+';
                    display: inline-block;
                    cursor: pointer;
                    margin-left: -1em;
                    min-width: 1em;
                }
            }

            &.open {
                .short {
                    display: none;
                }

                .long {
                    display: inline;
                }

                .more-less {
                    &:before {
                        content: '\2013';
                    }
                }
            }
        }
    }
}
@import 'docs-v2';
@import 'docs-v2-editor';
@import 'docs-config-editor'



